@import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Courier+Prime&display=swap");
.single-blog-wrapper * {
  margin: revert;
  padding: revert;
  list-style-type: revert;
  line-height: 1.5rem;
}
.single-blog-wrapper code, .single-blog-wrapper pre {
  border-radius: 5px;
  background-color: #282c34;
  color: #abb2bf;
}
.single-blog-wrapper code {
  padding: 0.2em 0.4em;
  line-break: auto;
  display: inline-block;
  line-height: 1.35;
}
.single-blog-wrapper pre {
  border: 1px solid #CCCCCC;
  overflow: auto;
  padding: 6px 10px;
}
.single-blog-wrapper blockquote {
  margin: 0;
  border-left: 5px solid #7a7a7a;
  font-style: italic;
  padding: 1.33em;
  text-align: left;
}
.single-blog-wrapper table {
  padding: 1rem 0;
}
.single-blog-wrapper th, .single-blog-wrapper td {
  padding: 6px 13px;
  border: 1px solid black;
}
.single-blog-wrapper hr {
  background: black;
}

.single-blog-wrapper {
  font-family: -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
}

